import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
// import InputLabel from '@material-ui/core/InputLabel';
// import MenuItem from '@material-ui/core/MenuItem';
// // import FormHelperText from '@material-ui/core/FormHelperText';
// import FormControl from '@material-ui/core/FormControl';
// import Select from '@material-ui/core/Select';
import { createModeleV, getAllModelesV, updateModeleV } from '../../store/epics/modelesActions'
// import { getAllMarquesV } from '../../store/epics/marquesActions'

const useStyles = makeStyles((theme) => ({
  formControl: {
    marginTop: theme.spacing(1),
    // minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export default function FormulaireModele() {
    const { modeleId } = useParams();

    const modelesV = useSelector((state) => state.modelesV);
    const { allModelesV } = modelesV;
    

    // const marquesV = useSelector((state) => state.marquesV);
    // const { allMarquesV } = marquesV;

    const dispatch = useDispatch();

    const [id, setId] = useState('');
    const [nomModele, setNomModele] = useState('');
    const [marqueModel, setMarqueModel] = useState('');

    const updateState = () => {
        if ( modeleId === 'add' )
        {   
          setNomModele('')
          setId('')
        } else {
          const selectedItem = allModelesV.find(item => item.id === Number(modeleId))
          setNomModele(selectedItem.nomModele)
          setMarqueModel(selectedItem.marqueModel)
          setId(selectedItem.id)
        }
    }
    // const getMarqueToModele = () => {
    //   dispatch(getAllMarquesV())
    // }

    useEffect(() => {
      dispatch(getAllModelesV())
      updateState()
    }, [dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = { id, nomModele, marqueModel }
        dispatch(createModeleV(dataToAdd))
    };

    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = { id, nomModele, marqueModel }
      dispatch(updateModeleV(dataToEdit))
    };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
                modeleId === 'add' ? (
                  <Button component={Link} to="/list-modeles-vehicules" className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                    <span className="hidden sm:flex">Ajouter un modèle</span>
                  </Button>
                ) : (
                  <Button component={Link} to="/list-modeles-vehicules" className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                    <span className="hidden sm:flex">Modifier un modèle</span>
                  </Button>
                )
            }
        </div>
        <form>
          <TextField  
            id="nomModele"
            value={nomModele}
            label="Nom Modèle"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomModele(e.target.value)}
          />
          <TextField  
            id="marqueModele"
            value={marqueModel}
            label="Marque"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setMarqueModel(e.target.value)}
          />
          {/* <FormControl variant="outlined" className={classes.formControl} fullWidth>
            <InputLabel id="vehiculeChauffeur">Marque</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="marqueModele"
              onChange={(e) => marqueModele(e.target.value)}
              label="Marque"
            >
              <MenuItem value="">
                <em>Select...</em>
              </MenuItem>
              {
                allMarquesV.map((marqueMod) => (
                  <MenuItem key={marqueMod.id}>{marqueMod.nomMarque}</MenuItem>
                ))
              }
            </Select>
          </FormControl> */}
        </form>
      </div>
    )
}
