import axios from 'axios'

const allTypesV = []

export const createTypeV = (typeV) => { 
  return (dispatch) => { 
      axios.post('http://62.171.157.49:7007/test/AddTypeVehicule', typeV)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...typeV,
              allTypesV: [...allTypesV, {...typeV}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_TYPEV"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeTypeV = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteTypeVehicule/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_TYPEV",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectTypeV = (typeV) => {
  return {
    type: "EDIT_TYPEV_ITEM",
    payload: typeV
  }
}

export const updateTypeV = (typeV) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateTypeVehicule/${typeV.id}`, { ...typeV })
    .then(() => {
      dispatch({
        type: "UPDATE_TYPEV"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getallTypesVSuccess = (allTypesV) => (
  {
    type: 'GET_ALL_TYPEV_SUCCESS',
    payload: allTypesV
  }
)
export const getallTypesV = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getTypeVehicules")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getallTypesVSuccess(res))
        return allTypesV
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}