/* eslint-disable no-shadow */
/* eslint-disable no-console */
import axios from 'axios'

let allChauffeurs = []

export const createChauffeur = (chauffeur) => { 
  return (dispatch) => { 
      axios.post('http://62.171.157.49:7007/test/AddChauffeur', chauffeur)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...chauffeur,
              allChauffeurs: [...allChauffeurs, {...chauffeur}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_CHAUFFEUR"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeChauffeur = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteChauffeur/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_CHAUFFEUR",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectChauffeur = (chauffeur) => {
  return {
    type: "EDIT_CHAUFFEUR_ITEM",
    chauffeur
  }
}

export const updateChauffeur = (chauffeur) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateChauffeur/${chauffeur.id}`, { ...chauffeur })
    .then(() => {
      dispatch({
        type: "UPDATE_CHAUFFEUR"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllChauffeursSuccess = (allChauffeurs) => (
  {
    type: 'GET_ALL_CHAUFFEUR_SUCCESS',
    payload: allChauffeurs
  }
)
export const getAllChauffeurs = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getChauffeurs")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllChauffeursSuccess(res))
        return allChauffeurs
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}