let initState = {
  allChauffeurs: [],
  chauffeurItem: {}
}

export  const chauffeursReducers = (state = initState, action) => {
  if (action.type === "GET_ALL_CHAUFFEUR_SUCCESS"){
    return {
      ...state,
      allChauffeurs: action.payload
    }
  }
  if (action.type === "REMOVE_CHAUFFEUR") {
    const {id} = action.payload
    return {
      allChauffeurs : [...state.allChauffeurs].filter((chauffeur) => chauffeur.id !== id)
    }
  }
  if (action.type === "EDIT_CHAUFFEUR_ITEM") {
    return {
      ...state,
      chauffeurItem: action.chauffeur
    }
  }
  return state
}