import React, { FC, useEffect, useState, Dispatch } from 'react';
import { makeStyles, CssBaseline, Box, Divider } from '@material-ui/core';
import {
  Dashboard as DashboardIcon,
  DriveEta,
  Person,
  Note,
  Apartment,
  People,
  LibraryBooks,
} from '@material-ui/icons';
import { AnyAction } from '@reduxjs/toolkit';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, Route, Switch, useLocation } from 'react-router-dom';

import {
  RootState,
  barTitlesActions,
  socketsActions,
} from 'store/modules';
import { SidebarItem } from 'utils/types';
import { Dashboard, Report } from 'components';


// import Footer from './Footer';
import TableTypes from 'components/TypesVehicules/TableTypes'
import FormulaireType from 'components/TypesVehicules/FormulaireType'
import TableVehicules from 'components/Vehicules/TableVehicules'
import FormulaireVehicule from 'components/Vehicules/FormulaireVehicule'
import TableChauffeurs from 'components/Chauffeurs/TableChauffeurs';
import FormulaireChauffeur from 'components/Chauffeurs/FormulaireChauffeur';
import TableDepartments from 'components/Department/TableDepartments';
import FormulaireDepartment from 'components/Department/FormulaireDepartment';
import TableTypesPermis from 'components/TypesPermis/TableTypesPermis';
import FormulaireTypePermis from 'components/TypesPermis/FormulaireTypePermis';
import TableDocV from 'components/DocumentsVehicules/TableDocV';
import FormulaireDocV from 'components/DocumentsVehicules/FormulaireDocV';
import TableDocOneV from 'components/Vehicules/TableDocOneV';
import TablePneusV from 'components/Vehicules/TablePneusV';
import TableComptes from 'components/Comptes/TableComptes';
import FormulaireComptes from 'components/Comptes/FormulaireComptes';
import TableMarque from 'components/MarqueVehicules/TableMarque';
import FormulaireMarque from 'components/MarqueVehicules/FormulaireMarque';
import TableModeles from 'components/ModelesVehicules/TableModeles';
import FormulaireModele from 'components/ModelesVehicules/FormulaireModele';
import TableVehiculesPermis from 'components/TypesPermis/TableVehiculesPermis';
import Sidebar from './Sidebar';
import Topbar from './Topbar';

const drawerWidth = 200;
const items: SidebarItem[] = [
  {
    key: 'dashboard',
    text: 'Dashboard',
    path: '/dashboard',
    icon: <DashboardIcon />,
  },
  {
    key: 'vehicles',
    text: 'Vehicules',
    path: '/list-vehicules',
    icon: <DriveEta />,
  },
  {
    key: 'typeVéhicules',
    text: 'Types des vehicules',
    path: '/list-types-vehicules',
    icon: <LibraryBooks />,
  },
  {
    key: 'marqueVéhicules',
    text: 'Marques des vehicules',
    path: '/list-marques-vehicules',
    icon: <LibraryBooks />,
  },
  {
    key: 'modelesVéhicules',
    text: 'Modèles des vehicules',
    path: '/list-modeles-vehicules',
    icon: <LibraryBooks />,
  },
  {
    key: 'chauffeurs',
    text: 'Chauffeurs',
    path: '/list-chauffeurs',
    icon: <People />,
  },
  {
    key: 'departments',
    text: 'Départments',
    path: '/list-departments',
    icon: <Apartment />,
  },
  {
    key: 'typesPermis',
    text: 'Types des permis',
    path: '/list-permis',
    icon: <Note />,
  },
  {
    key: 'docv',
    text: 'Documents',
    path: '/list-docv',
    icon: <Note />,
  },
  {
    key: 'comptes',
    text: 'Comptes',
    path: '/list-comptes',
    icon: <Person />,
  },
];

const useStyles = makeStyles(theme => ({
  hide: {
    display: 'none',
  },
  roots: {
    display: 'flex',
    flexDirection: 'row',
    height: '100vh',
  },
  main: {
    display: 'flex',
    flexDirection: 'column',
    flexGrow: 1,
    height: '100%',
  },
  footer: {
    alignContent: 'flex-end',
  },
}));

const MainRoutes: FC = () => (
  <Switch>
    <Route exact path="/dashboard" component={Dashboard} />
    <Route exact path="/list-chauffeurs" component={TableChauffeurs} />
    <Route exact path="/form-chauffeur/:chauffeurId?" component={FormulaireChauffeur} />
    <Route exact path="/list-vehicules" component={TableVehicules} />
    <Route exact path="/form-vehicule/:vehiculeId?" component={FormulaireVehicule} />
    <Route exact path="/list-types-vehicules" component={TableTypes} />
    <Route exact path="/form-type-vehicule/:typeId?" component={FormulaireType} />
    <Route exact path="/list-marques-vehicules" component={TableMarque} />
    <Route exact path="/form-marque-vehicule/:marqueId?" component={FormulaireMarque} />
    <Route exact path="/list-modeles-vehicules" component={TableModeles} />
    <Route exact path="/form-modele-vehicule/:modeleId?" component={FormulaireModele} />
    <Route exact path="/list-departments" component={TableDepartments} />
    <Route exact path="/form-department/:departmentId?" component={FormulaireDepartment} />
    <Route exact path="/list-permis" component={TableTypesPermis} />
    <Route exact path="/form-permis/:permisId?" component={FormulaireTypePermis} />
    <Route exact path="/list-vehicule-permis/:permisVehiculeId?" component={TableVehiculesPermis} />
    <Route exact path="/list-docv" component={TableDocV} />
    <Route exact path="/form-docv/:docId?" component={FormulaireDocV} />
    <Route exact path="/list-doconev" component={TableDocOneV} />
    <Route exact path="/list-pneusv" component={TablePneusV} />
    <Route exact path="/list-comptes" component={TableComptes} />
    <Route exact path="/form-compte/:compteId?" component={FormulaireComptes} />
    <Redirect to="/dashboard" />
  </Switch>
);

const getTitle = (state: RootState) => state.barTitles.title;

const useBarTitle = (dispatch: Dispatch<AnyAction>) => {
  const location = useLocation();
  const title = useSelector(getTitle);

  useEffect(() => {
    const item = items.find(el => el.path === location.pathname);
    if (item) {
      dispatch(barTitlesActions.setBarTitle(item.text));
    }
  }, [location, dispatch]);

  return title;
};

const useFetchData = (dispatch: Dispatch<AnyAction>) => {
  useEffect(() => {
    dispatch(socketsActions.start());
  }, [dispatch]);
};

const MainLayout: FC = () => {
  const { main, roots, footer } = useStyles();
  const [open, setOpen] = useState(true);
  const dispatch = useDispatch();

  useFetchData(dispatch);
  const barTitle = useBarTitle(dispatch);

  return (
    <div className={roots}>
      <CssBaseline />
      <Sidebar maxWidth={drawerWidth} opened={open} items={items} />
      <div className={main}>
        <Topbar
          title={barTitle}
          maxDrawerWidth={drawerWidth}
          drawerOpened={open}
          onDrawerOpen={() => setOpen(true)}
          onDrawerClose={() => setOpen(false)}
        />

        <Box p={3} style={{ flexGrow: 1 }}>
          <MainRoutes />
        </Box>
        <Divider />
        <Box py={2} px={3}>
          {/* <Footer className={footer} /> */}
        </Box>
      </div>
    </div>
  );
};

export default MainLayout;
