/* eslint-disable no-console */
import axios from 'axios'

let allVehicules = []

export const createVehicule = (vehicule) => { 
  return (dispatch) => { 
      axios.post('http://62.171.157.49:7007/test/AddVehicule', vehicule)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...vehicule,
              allVehicules: [...allVehicules, {...vehicule}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_VEHICULE"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeVehicule = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteVehicule/${  id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_VEHICULE",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectVehicule = (vehicule) => {
  return {
    type: "EDIT_VEHICULE_ITEM",
    vehicule
  }
}

export const updateVehicule = (vehicule) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateVehicule/${vehicule.id}`, { ...vehicule })
    .then(() => {
      dispatch({
        type: "UPDATE_VEHICULE"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllVehiculesSuccess = (allVehicules) => (
  {
    type: 'GET_ALL_VEHICULE_SUCCESS',
    payload: allVehicules
  }
)
export const getAllVehicules = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getVehicules")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllVehiculesSuccess(res))
        return allVehicules
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}