/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react/no-unescaped-entities */
import React, { useEffect } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { useDispatch, useSelector } from 'react-redux';
import {Link} from 'react-router-dom';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { removeChauffeur, selectChauffeur, getAllChauffeurs } from '../../store/epics/chauffeursActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabedit: {
    marginLeft: 10
  },
  fabdelete: {
    backgroundColor: 'red'
  },
});

export default function TableChauffeurs() {
    const classes = useStyles();

    const chauffeur = useSelector((state) => state.chauffeur);
    const { allChauffeurs } = chauffeur;

    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllChauffeurs())
    }, [dispatch])

    const deleteHandler = (item) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeChauffeur(item.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Nom de la famille</StyledTableCell>
              <StyledTableCell align="right">Prénom</StyledTableCell>
              <StyledTableCell align="right">Etat</StyledTableCell>
              <StyledTableCell align="right">Véhicule utilité</StyledTableCell>
              <StyledTableCell align="right">Département</StyledTableCell>
              <StyledTableCell align="right">Code personnel</StyledTableCell>
              <StyledTableCell align="right">E-mail</StyledTableCell>
              <StyledTableCell align="right">Numéro de GSM</StyledTableCell>
              <StyledTableCell align="right">        
                <Button component={Link} to="/form-chauffeur/add" variant="contained" size="small">
                  Ajouter Chauffeur
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allChauffeurs.map((chauf) => (
              <StyledTableRow key={chauf.id}>
                <StyledTableCell component="th" scope="row">{chauf.nomChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.prenomChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.etatChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.vehiculeChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.depChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.codeAuthChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.emailChauffeur}</StyledTableCell>
                <StyledTableCell align="right">{chauf.numPhoneChauffeur}</StyledTableCell>
                <StyledTableCell align="right">
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    className={classes.fabedit} 
                    component={Link} 
                    to={'/form-chauffeur/' + chauf.id}
                    onClick={() => selectChauffeur(chauf)}
                    >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    aria-label="delete" 
                    color="primary" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(chauf)}>
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
