/* eslint-disable import/named */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import { createDocument, updateDocument, getAllDocuments } from '../../store/epics/documentsActions'

export default function FormulaireDocV() {
    const { docId } = useParams();

    const dispatch = useDispatch();

    const document = useSelector((state) => state.document);
    const { allDocuments } = document;

    const [id, setId] = useState('');
    const [nomDocument, setNomDocument] = useState('');
    const [dateValDocument, setDateValDocument] = useState('');
    const [etatPayDocument, setEtatPayDocument] = useState('');

    const updateState = () => {
        if ( docId === 'add' )
        {  
          setId('')
          setNomDocument('')
          setDateValDocument('')
          setEtatPayDocument('')
        } 
        else 
        {   
          const selectedItem = allDocuments.find(item => item.id === Number(docId))
          setId(selectedItem.docId)
          setNomDocument(selectedItem.nomDocument)
          setDateValDocument(selectedItem.dateValDocument)
          setEtatPayDocument(selectedItem.etatPayDocument)
        }
    }

    useEffect(() => {
        dispatch(getAllDocuments())
        updateState()
    }, [dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = { nomDocument, dateValDocument, etatPayDocument }
        dispatch(createDocument(dataToAdd))
    };

    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = { id, nomDocument, dateValDocument, etatPayDocument }
      dispatch(updateDocument(dataToEdit))
    };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
                docId === 'add' ? (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                    <span className="hidden sm:flex">Ajouter un document</span>
                  </Button>
                ) : (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                    <span className="hidden sm:flex">Modifier un document</span>
                  </Button>
                )
            }
        </div>
        <form>
          <TextField  
            id="nomDocument"
            value={nomDocument}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomDocument(e.target.value)}
          />
          <TextField
            id="dateValDocument"
            value={dateValDocument}
            label="Date d'éxpiration"
            type="date"
            defaultValue="2017-05-24"
            // className={classes.textField}
            onChange={(e) => setDateValDocument(e.target.value)}
            InputLabelProps={{
              shrink: true,
            }}
            fullWidth
          />
          <TextField  
            id="etatPayDocument"
            value={etatPayDocument}
            label="Etat de payement"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setEtatPayDocument(e.target.value)}
          />
        </form>
      </div>
    )
}
