/* eslint-disable no-shadow */
/* eslint-disable no-redeclare */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { withStyles, makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { getAllTypesPermis, getAllTypesPermisSuccess } from '../../store/epics/typesPermisActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);
const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabdelete: {
    backgroundColor: 'red'
  },
  btnliste1: {
    backgroundColor: 'grey',
    color: 'white'
  }
});


export default function TableVehiculesPermis() {
    const { permisVehiculeId } = useParams();
    const classes = useStyles();
    
    const typePermis = useSelector((state) => state.typePermis);
    const { allTypesPermis, typesPermisItem } = typePermis;
    
    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllTypesPermis())
    }, [dispatch])

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Matricule</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allTypesPermis.map((typePermis) => (
              <StyledTableRow key={typePermis.id}>
                <StyledTableCell component="th" scope="row">{typePermis.name}</StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  <Button component={Link} to="/list-vehicule-permis" aria-label="add" className={classes.btnliste1}>
                    Liste
                  </Button>
                </StyledTableCell>
                <StyledTableCell align="right">
                  <Fab size="small" color="primary" aria-label="edit" className={classes.fabedit} component={Link} to="/form-permis/edit">
                    <EditIcon />
                  </Fab>
                  <Fab size="small" aria-label="delete" color="primary" className={classes.fabdelete}>
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
