import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { 
    Button,
    TextField
} from '@material-ui/core';
// import Input from '@material-ui/core/Input';
// import InputLabel from '@material-ui/core/InputLabel';
// import MenuItem from '@material-ui/core/MenuItem';
// import FormControl from '@material-ui/core/FormControl';
// import ListItemText from '@material-ui/core/ListItemText';
// import Select from '@material-ui/core/Select';
// import Checkbox from '@material-ui/core/Checkbox';
// import Chip from '@material-ui/core/Chip';
import { createTypesPermis, updateTypesPermis, getAllTypesPermis } from '../../store/epics/typesPermisActions'
// import { getAllVehicules } from '../../store/epics/vehiculesActions'

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

export default function FormulaireTypePermis() {
    const { permisId } = useParams();

    const typePermis = useSelector((state) => state.typePermis);
    const { allTypesPermis } = typePermis;
    
    // const vehicule = useSelector((state) => state.vehicule);
    // const { allVehicules, vehiculeItem, vehiculesItems } = vehicule;

    const dispatch = useDispatch();

    const [id, setId] = useState('');
    const [nomTypePermis, setNomTypePermis] = useState('');
    const [vehiculesPermis, setVehiculesPermis] = useState([]);
    const updateState = () => {
        if ( permisId === 'add' )
        {  
          setId('')
          setNomTypePermis('')
          setVehiculesPermis('')
        } 
        else 
        {   
          const selectedItem = allTypesPermis.find(item => item.id === Number(permisId))
          setId(selectedItem.id)
          setNomTypePermis(selectedItem.nomTypePermis)
          setVehiculesPermis(selectedItem.vehiculesPermis)
        }
    }
    // const getListVehicules = () => {
    //   dispatch(getAllVehicules())
    // }

    useEffect(() => {
        dispatch(getAllTypesPermis())
        // getListVehicules()
        updateState()
    }, [dispatch])

    const submitHandler = (e) => {
      e.preventDefault();
      const dataToAdd = { 
        nomTypePermis,
        vehiculesPermis
      }
      dispatch(createTypesPermis(dataToAdd))
  };

  const editHandler = (e) => {
    e.preventDefault();
    const dataToEdit = { 
      id,
      nomTypePermis,
      vehiculesPermis
    }
    dispatch(updateTypesPermis(dataToEdit))
};

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
            permisId === 'add' ? (
              <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                <span className="hidden sm:flex">Ajouter un permis</span>
              </Button>
            ) : (
              <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                <span className="hidden sm:flex">Modifier un permis</span>
              </Button>
            )
          }
        </div>
        <form>
          <TextField  
            id="nomTypePermis"
            value={nomTypePermis}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomTypePermis(e.target.value)}
          />
          <TextField  
            id="vehiculesPermis"
            value={vehiculesPermis}
            label="Vehicule de permis"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setVehiculesPermis(e.target.value)}
          />
          {/* <FormControl className={classes.formControl}>
            <InputLabel id="demo-mutiple-chip-label">Vehicule</InputLabel>
            <Select
              labelId="demo-mutiple-chip-label"
              id="demo-mutiple-chip"
              fullWidth
              multiple
              value={allVehicules}
              onChange={(e) => setVehiculesPermis(e.target.value)}
              input={<Input id="select-multiple-chip" />}
              renderValue={(allVehicules) => (
                <div className={classes.chips}>
                  {allVehicules.map((vehicule) => (
                    <Chip key={vehicule.id} label={vehicule.matVehicule} className={classes.chip} />
                  ))}
                </div>
              )}
              MenuProps={MenuProps}
            >
              {allVehicules.map((vehicule) => (
                <MenuItem key={vehicule.id} value={vehicule.matVehicule} style={getStyles(matVehicule, vehiculesPermis, theme)} fullWidth>
                  {vehicule.matVehicule}
                </MenuItem>
              ))}
            </Select>
          </FormControl> */}
        </form>
      </div>
    )
}
