let initState = {
  allMarquesV: [],
  marqueVItem: {}
}
  
export  const marquesVReducers = (state = initState, action) => {
    if (action.type === "GET_ALL_MARQUEV_SUCCESS"){
      return {
        ...state,
        allMarquesV: action.payload
      }
    }
    if (action.type === "REMOVE_MARQUEV") {
      const {id} = action.payload
      return {
        allMarquesV : [...state.allMarquesV].filter((marqueV) => marqueV.id !== id)
      }
    }
    if (action.type === "EDIT_MARQUEV_ITEM") {
      return {
        ...state,
        marqueVItem: action.payload
      }
    }
    return state
  }
  