let initState = {
  allVehicules: [],
  vehiculeItem: {},
  vehiculesItems: []
}

export  const vehiculesReducers = (state = initState, action) => {
  if (action.type === "GET_ALL_VEHICULE_SUCCESS"){
    return {
      ...state,
      allVehicules: action.payload
    }
  }
  if (action.type === "REMOVE_VEHICULE") {
    const {id} = action.payload
    return {
      allVehicules : [...state.allVehicules].filter((vehicule) => vehicule.id !== id)
    }
  }
  if (action.type === "EDIT_VEHICULE_ITEM") {
    return {
      ...state,
      vehiculeItem: action.vehicule
    }
  }
  return state
}