/* eslint-disable no-undef */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import { createCompte, updateCompte, getAllComptes } from '../../store/epics/comptesActions'


export default function FormulaireComptes() {
    const { compteId } = useParams();

    const dispatch = useDispatch();

    const comptes = useSelector((state) => state.comptes);
    const { allComptes } = comptes;

    const [id, setId] = useState('');
    const [nomCompte, setNomCompte] = useState('');
    const [prenomCompte, setPrenomCompte] = useState('');
    const [loginCompte, setLoginCompte] = useState('');
    const [depAsuivreCompte, setDepAsuivreCompte] = useState('');

    const updateState = () => {
        if ( compteId === 'add' )
        {  
          setId('')
          setNomCompte('')
          setPrenomCompte('')
          setLoginCompte('')
          setDepAsuivreCompte('')
        } 
        else 
        {   
          const selectedItem = allComptes.find(item => item.id === Number(compteId))
          setId(selectedItem.id)
          setNomCompte(selectedItem.nomCompte)
          setPrenomCompte(selectedItem.prenomCompte)
          setLoginCompte(selectedItem.loginCompte)
          setDepAsuivreCompte(selectedItem.depAsuivreCompte)
        }
    }

    useEffect(() => {
        dispatch(getAllComptes())
        updateState()
    }, [dispatch])

    const submitHandler = (e) => {
      e.preventDefault();
      const dataToAdd = { 
        nomCompte,
        prenomCompte,
        loginCompte,
        depAsuivreCompte
      }
      dispatch(createCompte(dataToAdd))
  };

  const editHandler = (e) => {
    e.preventDefault();
    const dataToEdit = { 
      id,
      nomCompte,
      prenomCompte,
      loginCompte,
      depAsuivreCompte
    }
    dispatch(updateCompte(dataToEdit))
};

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
              compteId === 'add' ? (
                <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                  <span className="hidden sm:flex">Ajouter un compte</span>
                </Button>
              ) : (
                <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                  <span className="hidden sm:flex">Modifier un compte</span>
                </Button>
              )
            }
        </div>
        <form>
          <TextField  
            id="nomCompte"
            value={nomCompte}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomCompte(e.target.value)}
          />
          <TextField  
            id="prenomCompte"
            value={prenomCompte}
            label="Prénom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setPrenomCompte(e.target.value)}
          />
          <TextField  
            id="loginCompte"
            value={loginCompte}
            label="Login"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setLoginCompte(e.target.value)}
          />
          <TextField  
            id="depAsuivreCompte"
            value={depAsuivreCompte}
            label="Départment"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setDepAsuivreCompte(e.target.value)}
          />
        </form>
      </div>
    )
}
