/* eslint-disable no-console */
import axios from 'axios'

const allModelesV = []

export const createModeleV = (modeleV) => { 
  return (dispatch) => { 
      axios.post('http://62.171.157.49:7007/test/AddModelVehicule', modeleV)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...modeleV,
              allModelesV: [...allModelesV, {...modeleV}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_MODELEV"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeModeleV = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/modelVehicule/delete/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_MODELEV",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectModeleV = (modeleV) => {
  return {
    type: "EDIT_MODELEV_ITEM",
    modeleV
  }
}

export const updateModeleV = (modeleV) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateModelVehicule/${modeleV.id}`, { ...modeleV })
    .then(() => {
      dispatch({
        type: "UPDATE_MODELEV"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllModelesVSuccess = (allModelesV) => (
  {
    type: 'GET_ALL_MODELEV_SUCCESS',
    payload: allModelesV
  }
)
export const getAllModelesV = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getModelVehicules")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllModelesVSuccess(res))
        return allModelesV
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}