let initState = {
  allDepartements: [],
  departementItem: {}
}

export  const departementsReducers = (state = initState, action) => {
  if (action.type === "GET_ALL_DEPARTEMENT_SUCCESS"){
    return {
      ...state,
      allDepartements: action.payload
    }
  }
  if (action.type === "REMOVE_DEPARTEMENT") {
    const {id} = action.payload
    return {
      allDepartements : [...state.allDepartements].filter((departement) => departement.id !== id)
    }
  }
  if (action.type === "EDIT_DEPARTEMENT_ITEM") {
    return {
      ...state,
      departementItem: action.payload
    }
  }
  return state
}