/* eslint-disable react/no-unescaped-entities */
import React, { useEffect } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { useDispatch, useSelector } from 'react-redux';
import {Link} from 'react-router-dom';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { removeCompte, selectCompte, getAllComptes } from '../../store/epics/comptesActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabedit: {
    marginLeft: 10
  },
  fabdelete: {
    backgroundColor: 'red'
  },
});

export default function TableComptes() {
    const classes = useStyles();

    const comptes = useSelector((state) => state.comptes);
    const { allComptes } = comptes;

    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllComptes())
    }, [dispatch])

    const deleteHandler = (item) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeCompte(item.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Nom</StyledTableCell>
              <StyledTableCell align="right">Prénom</StyledTableCell>
              <StyledTableCell align="right">Login</StyledTableCell>
              <StyledTableCell align="right">Départment</StyledTableCell>
              <StyledTableCell align="right">        
                <Button component={Link} to="/form-compte/add" variant="contained" size="small">
                  Ajouter Compte
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allComptes.map((compte) => (
              <StyledTableRow key={compte.id}>
                <StyledTableCell component="th" scope="row">{compte.nomCompte}</StyledTableCell>
                <StyledTableCell align="right">{compte.prenomCompte}</StyledTableCell>
                <StyledTableCell align="right">{compte.loginCompte}</StyledTableCell>
                <StyledTableCell align="right">{compte.depAsuivreCompte}</StyledTableCell>
                <StyledTableCell align="right">
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    className={classes.fabedit} 
                    component={Link} 
                    to={'/form-compte/' + compte.id}
                    onClick={() => selectCompte(compte)}
                    >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    aria-label="delete" 
                    color="primary" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(compte)}
                    >
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
