/* eslint-disable import/no-unresolved */
/* eslint-disable import/named */
import { combineReducers } from '@reduxjs/toolkit';

import { authsReducers as auths } from './auths';
import { usersReducers as users } from './users';
import { socketsReducers as sockets } from './sockets';
import { devicesReducers as devices } from './devices';
import { positionsReducers as positions } from './positions';
import { reportReducers as report } from './reports';
import { serversReducers as servers } from './servers';
import { barTitlesReducers as barTitles } from './barTitles';
import { typeVReducers as typesV } from './typeVReducers'
import { vehiculesReducers as vehicule } from './vehiculesReducers'
import { chauffeursReducers as chauffeur } from './chauffeursReducers'
import { departementsReducers as departementV } from './departementsReducers'
import { typePermisReducers as typePermis } from './typePermisReducers'
import { marquesVReducers as marquesV } from './marquesVReducers'
import { modelesVReducers as modelesV } from './modelesVReducers'
import { comptesReducers as comptes } from './comptesReducers'
import { documentsReducers as document } from './documentsReducers'


export const rootReducer = combineReducers({
  auths,
  users,
  sockets,
  devices,
  positions,
  report,
  servers,
  barTitles,

  typesV,
  marquesV,
  modelesV,
  vehicule,
  chauffeur,
  departementV,
  typePermis,
  comptes,
  document
});

export type RootState = ReturnType<typeof rootReducer>;

export { authsActions } from './auths';
export { devicesActions } from './devices';
export { positionsActions } from './positions';
export { usersActions } from './users';
export { socketsActions } from './sockets';
export { reportActions } from './reports';
export { serversActions } from './servers';
export { barTitlesActions } from './barTitles';
