import axios from 'axios'

let allMarquesV = []
export const createMarqueV = (marqueV) => async (dispatch) => {
  try {
    axios.post('http://62.171.157.49:7007/test/AddMarqueVehicule', marqueV)
      .then((res) => {
        if (res.status === 200) {
          return {
            ...marqueV,
            allMarquesV: [...allMarquesV, {...marqueV}]
          }
      }
      })
      .then(() => {
        dispatch({
          type: 'ADD_MARQUEV'
        })
      })
  } catch (err) {
    console.log('error from add: ', err)
  }
};

export const removeMarqueV = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteMarqueVehicule/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_MARQUEV",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectMarqueV = (marqueV) => {
  return {
    type: "EDIT_MARQUEV_ITEM",
    payload: marqueV
  }
}

export const updateMarqueV = (marqueV) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateMarqueVehicule/${marqueV.id}`,  { ...marqueV })
    .then(() => {
      dispatch({
        type: "UPDATE_MARQUEV"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllMarquesVSuccess = (allMarquesV) => (
  {
    type: 'GET_ALL_MARQUEV_SUCCESS',
    payload: allMarquesV
  }
)
export const getAllMarquesV = () => {
    return (dispatch, getState) => {
      fetch("http://62.171.157.49:7007/test/getMarqueVehicules")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllMarquesVSuccess(res))
        return allMarquesV
      })
      .catch((err) => console.log("error fetching marque: ", err))
    }
}