/* eslint-disable react/no-unescaped-entities */
import React, { useEffect, useState } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { useDispatch, useSelector } from 'react-redux';
import {Link} from 'react-router-dom';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';
import ListIcon from '@material-ui/icons/List';

import { removeVehicule, selectVehicule, getAllVehicules } from '../../store/epics/vehiculesActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabadd: {
    backgroundColor: 'green'
  },
  fabdelete: {
    backgroundColor: 'red'
  },
  btnliste: {
    backgroundColor: 'grey'
  },
  btnliste1: {
    backgroundColor: 'grey',
    color: 'white'
  }
});

export default function TableVehicules() {
    const classes = useStyles();

    const vehicule = useSelector((state) => state.vehicule);
    const { allVehicules } = vehicule;

    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllVehicules())
    }, [dispatch])

    const deleteHandler = (item) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeVehicule(item.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Matricule</StyledTableCell>
              <StyledTableCell>Département</StyledTableCell>
              <StyledTableCell>Etat d'installation</StyledTableCell>
              <StyledTableCell>Chauffeur</StyledTableCell>
              <StyledTableCell>Marque</StyledTableCell>
              <StyledTableCell>Modèle</StyledTableCell>
              <StyledTableCell>Année</StyledTableCell>
              <StyledTableCell>Type d'énergie</StyledTableCell>
              <StyledTableCell>Equipement</StyledTableCell>
              <StyledTableCell>Numéro de puce</StyledTableCell>
              <StyledTableCell>Capacité de réservoir</StyledTableCell>
              <StyledTableCell>Kilométrage</StyledTableCell>
              <StyledTableCell>Conso du carburant</StyledTableCell>
              <StyledTableCell>        
                <Button component={Link} to="/form-vehicule/add" variant="contained" size="small">
                  Ajouter
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allVehicules.map((itemV) => (
              <StyledTableRow key={itemV.name}>
                <StyledTableCell component="th" scope="row">{itemV.matVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.depVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.etatInstVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.chaufVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.marqVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.modVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.annVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.typeEnergVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.equipeVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.numPuceVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.capReservVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.kiloVehicule}</StyledTableCell>
                <StyledTableCell>{itemV.consoTheoVehicule}</StyledTableCell>
                <StyledTableCell>
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    component={Link} 
                    to={'/form-vehicule/' + itemV.id}
                    onClick={() => selectVehicule(itemV)}
                    >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="delete" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(itemV)}
                    >
                    <DeleteIcon color="red" />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
