/* eslint-disable no-undef */
/* eslint-disable consistent-return */
/* eslint-disable no-shadow */
/* eslint-disable no-console */
import axios from 'axios'

let allDepartements = []

export const createDepartement = (departement) => { 
  return (dispatch) => { 
    axios.post("http://62.171.157.49:7007/test/AddDepartement", departement)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...departement,
              allDepartements: [...allDepartements, {...departement}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_DEPARTEMENT"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeDepartement = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteDepartement/${id}`)
    .then(() => {
      dispatch({
        type: "REMOVE_DEPARTEMENT",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectDepartement = (departement) => {
  return {
    type: "EDIT_DEPARTEMENT_ITEM",
    payload: departement
  }
}

export const updateDepartement = (departement) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateDepartement/${departement.id}`, { ...departement })
    .then(() => {
      dispatch({
        type: "UPDATE_DEPARTEMENT",
        payload: { departement }
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllDepartementsSuccess = (allDepartements) => (
  {
    type: 'GET_ALL_DEPARTEMENT_SUCCESS',
    payload: allDepartements
  }
)
export const getAllDepartements = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getDepartements")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllDepartementsSuccess(res))
        return allDepartements
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}