const initState = {
    allTypesV: [],
    typeVItem: {}
  }
  
export  const typeVReducers = (state = initState, action) => {
    if (action.type === "GET_ALL_TYPEV_SUCCESS"){
      return {
        ...state,
        allTypesV: action.payload
      }
    }
    if (action.type === "REMOVE_TYPEV") {
      const {id} = action.payload
      return {
        allTypesV : [...state.allTypesV].filter((typeV) => typeV.id !== id)
      }
    }
    if (action.type === "EDIT_TYPEV_ITEM") {
      return {
        ...state,
        typeVItem: action.payload
      }
    }
    return state
  }
  