/* eslint-disable no-redeclare */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { withStyles, makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import EditIcon from '@material-ui/icons/Edit';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';

import { removeDocument, selectDocument, getAllDocuments } from '../../store/epics/documentsActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);


const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabdelete: {
    backgroundColor: 'red'
  },
});


export default function TableDocV() {
    const classes = useStyles();

    const dispatch = useDispatch();

    const document = useSelector((state) => state.document);
    const { allDocuments } = document;

    useEffect(() => {
      dispatch(getAllDocuments())
    }, [dispatch])

    const deleteHandler = (item) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeDocument(item.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Nom</StyledTableCell>
              <StyledTableCell>Date</StyledTableCell>
              <StyledTableCell>Etat</StyledTableCell>
              <StyledTableCell align="right">        
                <Button component={Link} to="/form-docv/add" variant="contained" size="small">
                  Ajouter Document
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allDocuments.map((doc) => (
              <StyledTableRow key={doc.id}>
                <StyledTableCell component="th" scope="row">{doc.nomDocument}</StyledTableCell>
                <StyledTableCell component="th" scope="row">{doc.dateValDocument}</StyledTableCell>
                <StyledTableCell component="th" scope="row">{doc.etatPayDocument}</StyledTableCell>
                <StyledTableCell align="right">
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    className={classes.fabedit} 
                    component={Link} 
                    to={'/form-docv/' + doc.id}
                    onClick={() => selectDocument(doc)}
                    >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    aria-label="delete" 
                    color="primary" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(doc)}
                    >
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
