/* eslint-disable no-undef */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { createChauffeur, updateChauffeur, getAllChauffeurs } from '../../store/epics/chauffeursActions'
import { getAllVehicules } from '../../store/epics/vehiculesActions'
import { getAllDepartements } from '../../store/epics/departementsActions'


const useStyles = makeStyles((theme) => ({
  formControl: {
    marginTop: theme.spacing(1),
    // minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));


export default function FormulaireChauffeur() {
    const { chauffeurId } = useParams();

    const classes = useStyles();
    
    const chauffeur = useSelector((state) => state.chauffeur);
    const { allChauffeurs } = chauffeur;

    const vehicule = useSelector((state) => state.vehicule);
    const { allVehicules } = vehicule;
    
    const departementV = useSelector((state) => state.departementV);
    const { allDepartements } = departementV;

    const dispatch = useDispatch();

    const [id, setId] = useState('');
    const [nomChauffeur, setNomChauffeur] = useState('');
    const [prenomChauffeur, setPrenomChauffeur] = useState('');
    const [etatChauffeur, setEtatChauffeur] = useState('');
    const [vehiculeChauffeur, setVehiculeChauffeur] = useState('');
    const [depChauffeur, setDepChauffeur] = useState('');
    const [codeAuthChauffeur, setCodeAuthChauffeur] = useState('');
    const [emailChauffeur, setEmailChauffeur] = useState('');
    const [numPhoneChauffeur, setNumPhoneChauffeur] = useState('');

    const updateState = () => {
      if ( chauffeurId === 'add' )
      {  
        console.log(allVehicules)
        console.log(allDepartements)
        setId('')
        setNomChauffeur('')
        setPrenomChauffeur('')
        setEtatChauffeur('')
        setVehiculeChauffeur('')
        setDepChauffeur('')
        setCodeAuthChauffeur('')
        setEmailChauffeur('')
        setNumPhoneChauffeur('')
      } else 
      {
        const selectedItem = allChauffeurs.find(item => item.id === Number(chauffeurId))
        setId(selectedItem.id)
        setNomChauffeur(selectedItem.nomChauffeur)
        setPrenomChauffeur(selectedItem.prenomChauffeur)
        setEtatChauffeur(selectedItem.etatChauffeur)
        setVehiculeChauffeur(selectedItem.vehiculeChauffeur)
        setDepChauffeur(selectedItem.depChauffeur)
        setCodeAuthChauffeur(selectedItem.codeAuthChauffeur)
        setEmailChauffeur(selectedItem.emailChauffeur)
        setNumPhoneChauffeur(selectedItem.numPhoneChauffeur)
      }
    }

    // const getVehiculeToChauffeur = () => {
    //   dispatch(getAllVehicules())
    // }

    // const getDepartementToChauffeur = () => {
    //   dispatch(getAllDepartements())
    // }

    useEffect(() => {
        dispatch(getAllChauffeurs())
        // getVehiculeToChauffeur()
        // getDepartementToChauffeur()
        updateState()
    }, [dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = { 
          nomChauffeur, 
          prenomChauffeur, 
          etatChauffeur, 
          vehiculeChauffeur, 
          depChauffeur, 
          codeAuthChauffeur, 
          emailChauffeur, 
          numPhoneChauffeur 
        }
        dispatch(createChauffeur(dataToAdd))
    };

    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = { 
        id,
        nomChauffeur, 
        prenomChauffeur, 
        etatChauffeur, 
        vehiculeChauffeur, 
        depChauffeur, 
        codeAuthChauffeur, 
        emailChauffeur, 
        numPhoneChauffeur 
      }
      dispatch(updateChauffeur(dataToEdit))
  };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
              chauffeurId === 'add' ? (
                <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                  <span className="hidden sm:flex">Ajouter un Chauffeur</span>
                </Button>
              ) : (
                <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                  <span className="hidden sm:flex">Modifier un Chauffeur</span>
                </Button>
              )
            }
        </div>
        <form>
          <TextField  
            id="nomChauffeur"
            value={nomChauffeur}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomChauffeur(e.target.value)}
          />
          <TextField  
            id="prenomChauffeur"
            value={prenomChauffeur}
            label="Prénom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setPrenomChauffeur(e.target.value)}
          />
          <TextField  
            id="etatChauffeur"
            value={etatChauffeur}
            label="Etat de chauffeur"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setEtatChauffeur(e.target.value)}
          />
          <TextField  
            id="vehiculeChauffeur"
            value={vehiculeChauffeur}
            label="Véhicule de chauffeur"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setVehiculeChauffeur(e.target.value)}
          />
          <TextField  
            id="depChauffeur"
            value={depChauffeur}
            label="Département de chauffeur"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setDepChauffeur(e.target.value)}
          />
          {/* <FormControl variant="outlined" className={classes.formControl} fullWidth>
            <InputLabel id="vehiculeChauffeur">Véhicule utilisé</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="vehiculeChauffeur"
              onChange={(e) => setVehiculeChauffeur(e.target.value)}
              label="Type de vehicule"
            >
              <MenuItem value="">
                <em>Select...</em>
              </MenuItem>
              {
                allVehicules.map((vehiculeChauf) => (
                  <MenuItem key={vehiculeChauf.id} value={vehiculeChauf.matricule}>{vehiculeChauf.matricule}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <FormControl variant="outlined" className={classes.formControl} fullWidth>
            <InputLabel id="depChauffeur">Département</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="depChauffeur"
              onChange={(e) => setDepChauffeur(e.target.value)}
              label="Type de vehicule"
            >
              <MenuItem value="">
                <em>Select...</em>
              </MenuItem>
              {
                allDepartements.map((departementChauf) => (
                  <MenuItem key={departementChauf.id} value={departementChauf.nomDepartement}>{departementChauf.nomDepartement}</MenuItem>
                ))
              }
            </Select>
          </FormControl> */}
          <TextField  
            id="codeAuthChauffeur"
            value={codeAuthChauffeur}
            label="Code"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setCodeAuthChauffeur(e.target.value)}
          />
          <TextField  
            id="emailChauffeur"
            value={emailChauffeur}
            label="E-mail"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setEmailChauffeur(e.target.value)}
          />
          <TextField  
            id="numPhoneChauffeur"
            value={numPhoneChauffeur}
            label="Numéro GSM"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNumPhoneChauffeur(e.target.value)}
          />
        </form>
      </div>
    )
}
