/* eslint-disable no-undef */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { createVehicule, updateVehicule, getAllVehicules } from '../../store/epics/vehiculesActions'
import { getAllChauffeurs } from '../../store/epics/chauffeursActions'


export default function FormulaireVehicule() {
    const { vehiculeId } = useParams();

    const classes = makeStyles()

    const vehicule = useSelector((state) => state.vehicule);
    const { allVehicules } = vehicule

    const chauffeur = useSelector((state) => state.chauffeur);
    const { allChauffeurs } = chauffeur

    const dispatch = useDispatch();

    const [id, setId] = useState('');
    const [matVehicule, setMatVehicule] = useState('');
    const [depVehicule, setDepVehicule] = useState('');
    const [etatInstVehicule, setEtatInstVehicule] = useState('');
    const [chaufVehicule , setChaufVehicule] = useState('');
    const [marqVehicule, setMarqVehicule] = useState('');
    const [modVehicule, setModVehicule] = useState('');
    const [annVehicule, setAnnVehicule] = useState('');
    const [typeEnergVehicule, setTypeEnergVehicule] = useState('');
    const [equipeVehicule, setEquipeVehicule] = useState('');
    const [numPuceVehicule, setNumPuceVehicule] = useState('');
    const [capReservVehicule, setCapReservVehicule] = useState('');
    const [kiloVehicule, setKiloVehicule] = useState('');
    const [consoTheoVehicule, setConsoTheoVehicule] = useState('');

    const updateState = () => {
      if ( vehiculeId === 'add' )
        {   
          setId('')
          setMatVehicule('')
          setDepVehicule('')
          setEtatInstVehicule('')
          setChaufVehicule('')
          setMarqVehicule('')
          setModVehicule('')
          setAnnVehicule('')
          setTypeEnergVehicule('')
          setEquipeVehicule('')
          setNumPuceVehicule('')
          setCapReservVehicule('')
          setKiloVehicule('')
          setConsoTheoVehicule('')
        } else {
          const selectedItem = allVehicules.find(item => item.id === Number(vehiculeId))
          setId(selectedItem.id)
          setMatVehicule(selectedItem.matVehicule)
          setDepVehicule(selectedItem.depVehicule)
          setEtatInstVehicule(selectedItem.etatInstVehicule)
          setChaufVehicule(selectedItem.chaufVehicule)
          setMarqVehicule(selectedItem.marqVehicule)
          setModVehicule(selectedItem.modVehicule)
          setAnnVehicule(selectedItem.annVehicule)
          setTypeEnergVehicule(selectedItem.typeEnergVehicule)
          setEquipeVehicule(selectedItem.equipeVehicule)
          setNumPuceVehicule(selectedItem.numPuceVehicule)
          setCapReservVehicule(selectedItem.capReservVehicule)
          setKiloVehicule(selectedItem.kiloVehicule)
          setConsoTheoVehicule(selectedItem.consoTheoVehicule)
        }
    }
  
    useEffect(() => {
        dispatch(getAllVehicules())
        dispatch(getAllChauffeurs())
        updateState()
    }, [dispatch])
  
    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = {
          matVehicule,
          depVehicule,
          etatInstVehicule,
          chaufVehicule,
          marqVehicule,
          modVehicule,
          annVehicule,
          typeEnergVehicule,
          equipeVehicule,
          numPuceVehicule,
          capReservVehicule,
          kiloVehicule,
          consoTheoVehicule
         }
        dispatch(createVehicule(dataToAdd))
    };
  
    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = {
        id,
        matVehicule,
        depVehicule,
        etatInstVehicule,
        chaufVehicule,
        marqVehicule,
        modVehicule,
        annVehicule,
        typeEnergVehicule,
        equipeVehicule,
        numPuceVehicule,
        capReservVehicule,
        kiloVehicule,
        consoTheoVehicule
      }
      dispatch(updateVehicule(dataToEdit))
    };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
              vehiculeId === 'add' ? (
                <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                  <span className="hidden sm:flex">Ajouter une Vehicule</span>
                </Button>
              ) : (
                <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                  <span className="hidden sm:flex">Modifier une Vehicule</span>
                </Button>
              )
            }
        </div>
        <form>
          <TextField  
            id="matVehicule"
            value={matVehicule}
            label="Matricule"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setMatVehicule(e.target.value)}
          />
          <TextField  
            id="depVehicule"
            value={depVehicule}
            label="Département"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setDepVehicule(e.target.value)}
          />
          <TextField  
            id="etatInstVehicule"
            value={etatInstVehicule}
            label="Etat d'installation"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setEtatInstVehicule(e.target.value)}
          />
          {/* <TextField  
            id="chaufVehicule"
            value={chaufVehicule}
            label="Etat d'installation"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setChaufVehicule(e.target.value)}
          /> */}
          {/* <FormControl variant="outlined" className={classes.formControl} fullWidth>
            <InputLabel id="demo-simple-select-outlined-label">Selectionner un chauffeur</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="chaufVehicule "
              onChange={(e) => setChaufVehicule (e.target.value)}
              label="Type de vehicule"
            >
              <MenuItem value="">
                <em>Select...</em>
              </MenuItem>
              {
                allChauffeurs.map((chauf) => (
                  <MenuItem key={chauf.id}>{chauf.nomChauffeur} {chauf.prenomChauffeur}</MenuItem>
                ))
              }
            </Select>
          </FormControl> */}
          <TextField  
            id="chaufVehicule"
            value={chaufVehicule}
            label="Chauffeur"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setChaufVehicule(e.target.value)}
          />
          <TextField  
            id="marqVehicule"
            value={marqVehicule}
            label="Marque"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setMarqVehicule(e.target.value)}
          />
          <TextField  
            id="modVehicule"
            value={modVehicule}
            label="Modèle"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setModVehicule(e.target.value)}
          />
          <TextField  
            id="annVehicule"
            value={annVehicule}
            label="Année"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setAnnVehicule(e.target.value)}
          />
          <TextField  
            id="typeEnergvehicule"
            value={typeEnergVehicule}
            label="Type d'énergie"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setTypeEnergVehicule(e.target.value)}
          />
          <TextField
            id="equipVehicule"
            value={equipeVehicule}
            label="Equipement"
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"
            fullWidth
            onChange={(e) => setEquipeVehicule(e.target.value)}
          />
          <TextField
            id="numPuceVehicule"
            value={numPuceVehicule}
            label="Numéro de puce"
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"
            fullWidth
            onChange={(e) => setNumPuceVehicule(e.target.value)}
          />
          <TextField
            id="capReservVehicule"
            value={capReservVehicule}
            label="Capacité de réservoir"
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"
            fullWidth
            onChange={(e) => setCapReservVehicule(e.target.value)}
          />
          <TextField
            id="kiloVehicule"
            value={kiloVehicule}
            label="Kilométrage"
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"
            fullWidth
            onChange={(e) => setKiloVehicule(e.target.value)}
          />
          <TextField
            id="consoTheoVehicule"
            value={consoTheoVehicule}
            label="Consommation théorique du carburant"
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"
            fullWidth
            onChange={(e) => setConsoTheoVehicule(e.target.value)}
          />
          {/* <FormControl variant="outlined" className={classes.formControl} fullWidth>
            <InputLabel id="demo-simple-select-outlined-label">Selectionner un départment</InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              onChange={(e) => setNumPuce(e.target.value)}
              label="Type de vehicule"
            >
              <MenuItem value="">
                <em>Select...</em>
              </MenuItem>
              <MenuItem value={10}>Ten</MenuItem>
              <MenuItem value={20}>Twenty</MenuItem>
              <MenuItem value={30}>Thirty</MenuItem>
            </Select>
          </FormControl> */}
        </form>
      </div>
    )
}
