let initState = {
  allDocuments: [],
  documentItem: {}
}

export  const documentsReducers = (state = initState, action) => {
  if (action.type === "GET_ALL_DOCUMENT_SUCCESS"){
    return {
      ...state,
      allDocuments: action.payload
    }
  }
  if (action.type === "REMOVE_DOCUMENT") {
    const {id} = action.payload
    return {
      allDocuments : [...state.allDocuments].filter((document) => document.id !== id)
    }
  }
  if (action.type === "EDIT_DOCUMENT_ITEM") {
    return {
      ...state,
      documentItem: action.payload
    }
  }
  return state
}