/* eslint-disable no-redeclare */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { withStyles, makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { removeTypesPermis, selectTypesPermis, getAllTypesPermis } from '../../store/epics/typesPermisActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabdelete: {
    backgroundColor: 'red'
  },
  btnliste1: {
    backgroundColor: 'grey',
    color: 'white'
  }
});


export default function TableTypesPermis() {
    const classes = useStyles();
    
    const typePermis = useSelector((state) => state.typePermis);
    const { allTypesPermis } = typePermis;
    
    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllTypesPermis())
    }, [dispatch])

    const deleteHandler = (item) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeTypesPermis(item.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Nom</StyledTableCell>
              <StyledTableCell>Liste vehicules</StyledTableCell>
              <StyledTableCell align="right">        
                <Button component={Link} to="/form-permis/add" variant="contained" size="small">
                  Ajouter Permis
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allTypesPermis.map((itemType) => (
              <StyledTableRow key={itemType.id}>
                <StyledTableCell component="th" scope="row">{itemType.nomTypePermis}</StyledTableCell>
                <StyledTableCell component="th" scope="row">
                  {/* <Button component={Link} to={`/list-vehicule-permis/${typePermis.id}`} aria-label="add" className={classes.btnliste1}>
                    Liste
                  </Button> */}
                  {itemType.vehiculesPermis}
                </StyledTableCell>
                <StyledTableCell align="right">
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    className={classes.fabedit} 
                    component={Link} 
                    to={'/form-permis/' + itemType.id}
                    onClick={() => selectTypesPermis(itemType)}
                    >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    aria-label="delete" 
                    color="primary" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(itemType)}
                    >
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
