const initState = {
    allTypesPermis: [],
    typesPermisItem: {}
  }
  
export  const typePermisReducers = (state = initState, action) => {
    if (action.type === "GET_ALL_TYPES_PERMIS_SUCCESS"){
      return {
        ...state,
        allTypesPermis: action.payload
      }
    }
    if (action.type === "REMOVE_TYPES_PERMIS") {
      const {id} = action.payload
      return {
        allTypesPermis : [...state.allTypesPermis].filter((typesPermis) => typesPermis.id !== id)
      }
    }
    if (action.type === "EDIT_TYPES_PERMIS_ITEM") {
      return {
        ...state,
        typesPermisItem: action.typesPermis
      }
    }
    return state
  }
  