/* eslint-disable import/named */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import { createTypeV, getallTypesV, updateTypeV } from '../../store/epics/typesActions'

export default function FormulaireType() {
    const { typeId } = useParams();

    const dispatch = useDispatch();

    const typesV = useSelector((state) => state.typesV);
    const { allTypesV } = typesV

    const [nomTypeVehicule, setNomTypeVehicule] = useState('');
    const [id, setId] = useState('');

    const updateState = () => {
      if ( typeId === 'add' )
        {   
          setNomTypeVehicule('')
          setId('')
        } else {
          const selectedItem = allTypesV.find(item => item.id === Number(typeId))
          setNomTypeVehicule(selectedItem.nomTypeVehicule)
          setId(selectedItem.id)
        }
    }

    useEffect(() => {
      dispatch(getallTypesV())
      updateState()
    }, [dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = { nomTypeVehicule }
        dispatch(createTypeV(dataToAdd))
    };

    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = { id, nomTypeVehicule }
      dispatch(updateTypeV(dataToEdit))
  };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
            typeId === 'add' ? (
              <Button component={Link} to="/list-types-vehicules" className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                <span className="hidden sm:flex">Ajouter un type</span>
              </Button>
            ) : (
              <Button component={Link} to="/list-types-vehicules" className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                <span className="hidden sm:flex">Modifier un type</span>
              </Button>
            )
          }
        </div>
        <form>
          <TextField  
            id="nomTypeVehicule"
            value={nomTypeVehicule}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomTypeVehicule(e.target.value)}
          />
        </form>
      </div>
    )
}
