let initState = {
  allModelesV: [],
  modeleVItem: {}
}
  
export  const modelesVReducers = (state = initState, action) => {
    if (action.type === "GET_ALL_MODELEV_SUCCESS"){
      return {
        ...state,
        allModelesV: action.payload
      }
    }
    if (action.type === "REMOVE_MODELEV") {
      const {id} = action.payload
      return {
        allModelesV : [...state.allModelesV].filter((modeleV) => modeleV.id !== id)
      }
    }
    if (action.type === "EDIT_MMODELEV_ITEM") {
      return {
        ...state,
        modeleVItem: action.modeleV
      }
    }
    return state
  }
  