import axios from 'axios'

let allDocuments = [] 

export const createDocument = (document) => { 
  return (dispatch) => { 
    axios.post("http://62.171.157.49:7007/test/AddDocument", document)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...document,
              allDocuments: [...allDocuments, {...document}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_DOCUMENT",
            payload: document
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeDocument = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteDocument/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_DOCUMENT",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectDocument = (document) => {
  return {
    type: "EDIT_DOCUMENT_ITEM",
    payload: document
  }
}

export const updateDocument = (document) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateDocument/${document.id}`, { ...document })
    .then(() => {
      dispatch({
        type: "UPDATE_DOCUMENT",
        payload: { document }
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllDocumentsSuccess = (allDocuments) => (
  {
    type: 'GET_ALL_DOCUMENT_SUCCESS',
    payload: allDocuments
  }
)
export const getAllDocuments = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getDocuments")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllDocumentsSuccess(res))
        return allDocuments
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}