/* eslint-disable no-shadow */
/* eslint-disable no-console */
import axios from 'axios'

let allComptes = []

export const createCompte = (compte) => { 
  return (dispatch) => { 
    axios.post("http://62.171.157.49:7007/test/AddCompte", compte)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...compte,
              allComptes: [...allComptes, {...compte}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_COMPTE",
            payload: compte
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeCompte = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteCompte/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_COMPTE",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectCompte = (compte) => {
  return {
    type: "EDIT_COMPTE_ITEM",
    payload: compte
  }
}

export const updateCompte = (compte) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateCompte/${compte.id}`, { ...compte })
    .then(() => {
      dispatch({
        type: "UPDATE_COMPTE",
        payload: { compte }
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllComptesSuccess = (allComptes) => (
  {
    type: 'GET_ALL_COMPTE_SUCCESS',
    payload: allComptes
  }
)
export const getAllComptes = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getComptes")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllComptesSuccess(res))
        return allComptes
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}