/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-redeclare */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import {Link, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { withStyles, makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { removeTypeV, selectTypeV, getallTypesVSuccess, getallTypesV } from '../../store/epics/typesActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
  createData('Eclair', 262, 16.0, 24, 6.0),
  createData('Cupcake', 305, 3.7, 67, 4.3),
  createData('Gingerbread', 356, 16.0, 49, 3.9),
];

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabdelete: {
    backgroundColor: 'red'
  },
});

export default function TableTypes() {
    const classes = useStyles();

    const typesV = useSelector((state) => state.typesV);
    const { allTypesV, typeVItem } = typesV;

    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getallTypesV())
    }, [dispatch])

    const deleteHandler = (typeVehic) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeTypeV(typeVehic.id));
      }
    };

    return (
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Nom</StyledTableCell>
              <StyledTableCell align="right">        
                <Button component={Link} to="/form-type-vehicule/add" variant="contained" size="small">
                  Ajouter Type
                </Button>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allTypesV.map((typeVehic) => (
              <StyledTableRow key={typeVehic.id}>
                <StyledTableCell component="th" scope="row">
                  {typeVehic.nomTypeVehicule}
                </StyledTableCell>
                <StyledTableCell align="right">
                  <Fab 
                    size="small" 
                    color="primary" 
                    aria-label="edit" 
                    className={classes.fabedit} 
                    component={Link}
                    to={'/form-type-vehicule/' + typeVehic.id}
                    onClick={() => selectTypeV(typeVehic)}
                  >
                    <EditIcon />
                  </Fab>
                  <Fab 
                    size="small" 
                    aria-label="delete" 
                    color="primary" 
                    className={classes.fabdelete}
                    onClick={() => deleteHandler(typeVehic)}
                  >
                    <DeleteIcon />
                  </Fab>
                </StyledTableCell>
              </StyledTableRow>
          ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
}
