let initState = {
  allComptes: [],
  compteItem: {}
}

export  const comptesReducers = (state = initState, action) => {
  if (action.type === "GET_ALL_COMPTE_SUCCESS"){
    return {
      ...state,
      allComptes: action.payload
    }
  }
  if (action.type === "REMOVE_COMPTE") {
    const {id} = action.payload
    return {
      allComptes : [...state.allComptes].filter((compte) => compte.id !== id)
    }
  }
  if (action.type === "EDIT_COMPTE_ITEM") {
    return {
      ...state,
      compteItem: action.payload
    }
  }
  return state
}