/* eslint-disable no-console */
import axios from 'axios'

const allTypesPermis = []

export const createTypesPermis = (typesPermis) => { 
  return (dispatch) => { 
      axios.post('http://62.171.157.49:7007/test/AddTypePermis', typesPermis)
        .then((res) => {
          if (res.status === 200) {
            return {
              ...typesPermis,
              allTypesPermis: [...allTypesPermis, {...typesPermis}]
            }
        }
        })
        .then(() => {
          dispatch({
            type: "ADD_TYPES_PERMIS"
          })
        })
        .catch((err) => console.log('error from add: ', err))
    }
}

export const removeTypesPermis = (id) => {
  return (dispatch) => {
    axios.delete(`http://62.171.157.49:7007/test/DeleteTypePermis/${id}`)
    .then(() => {
      console.log(id)
      dispatch({
        type: "REMOVE_TYPES_PERMIS",
        payload: { id }
      })
    })
    .catch((err) => console.log('error from delete: ',err))
  }
}

export const selectTypesPermis = (typesPermis) => {
  return {
    type: "EDIT_TYPES_PERMIS_ITEM",
    typesPermis
  }
}

export const updateTypesPermis = (typesPermis) => {
  return (dispatch) => {
    axios.put(`http://62.171.157.49:7007/test/UpdateTypePermis/${typesPermis.id}`, { ...typesPermis })
    .then(() => {
      dispatch({
        type: "UPDATE_TYPES_PERMIS"
      })
    })
    .catch((err) => console.log(err))
  }
}

export const getAllTypesPermisSuccess = (allTypesPermis) => (
  {
    type: 'GET_ALL_TYPES_PERMIS_SUCCESS',
    payload: allTypesPermis
  }
)
export const getAllTypesPermis = () => {
    return (dispatch) => {
      fetch("http://62.171.157.49:7007/test/getTypePermis")
      .then((res) => 
        res.json()
      )
      .then((res) => {
        dispatch(getAllTypesPermisSuccess(res))
        return allTypesPermis
      })
      .catch((err) => console.log("error adding agent: ", err))
    }
}
