/* eslint-disable import/named */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { 
    Button,
    TextField
} from '@material-ui/core';
import { createDepartement, getAllDepartements, updateDepartement } from '../../store/epics/departementsActions'

export default function FormulaireDepartment() {
    const { departmentId } = useParams();

    const departementV = useSelector((state) => state.departementV);
    const { allDepartements } = departementV

    const dispatch = useDispatch();

    const [nomDepartement, setNomDepartement] = useState('');
    const [id, setId] = useState('');

    const updateDepartmentsState = () => {
      if ( departmentId === 'add' )
        {   
          setNomDepartement('')
          setId('')
        } else {
          const selectedItem = allDepartements.find(item => item.id === Number(departmentId))
          setNomDepartement(selectedItem.nomDepartement)
          setId(selectedItem.id)
        }
    }

    useEffect(() => {
        dispatch(getAllDepartements())
        updateDepartmentsState()
    }, [dispatch])

    const submitHandler = (e) => {
        e.preventDefault();
        const dataToAdd = { nomDepartement }
        dispatch(createDepartement(dataToAdd))
    };

    const editHandler = (e) => {
      e.preventDefault();
      const dataToEdit = { id, nomDepartement }
      dispatch(updateDepartement(dataToEdit))
    };

    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
                departmentId === 'add' ? (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                    <span className="hidden sm:flex">Ajouter un department</span>
                  </Button>
                ) : (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                    <span className="hidden sm:flex">Modifier un department</span>
                  </Button>
                )
            }
        </div>
        <form>
          <TextField  
            id="nomDepartement"
            value={nomDepartement}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomDepartement(e.target.value)}
          />
        </form>
      </div>
    )
}
