/* eslint-disable import/named */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import { 
    Button,
    TextField
} from '@material-ui/core';
import { createMarqueV, getAllMarquesV, updateMarqueV } from '../../store/epics/marquesActions'

export default function FormulaireMarque() {
  const { marqueId } = useParams();

  const marquesV = useSelector((state) => state.marquesV);
  const { allMarquesV } = marquesV

  const dispatch = useDispatch();

  const [nomMarque, setNomMarque] = useState('');
  const [id, setId] = useState('');

  const updateState = () => {
    if ( marqueId === 'add' )
      {   
        setNomMarque('')
        setId('')
      } else {
        const selectedItem = allMarquesV.find(item => item.id === Number(marqueId))
        setNomMarque(selectedItem.nomMarque)
        setId(selectedItem.id)
      }
  }

  useEffect(() => {
      dispatch(getAllMarquesV())
      updateState()
  }, [dispatch])

  const submitHandler = (e) => {
      e.preventDefault();
      const dataToAdd = { nomMarque }
      dispatch(createMarqueV(dataToAdd))
  };

  const editHandler = (e) => {
    e.preventDefault();
    const dataToEdit = { id, nomMarque }
    dispatch(updateMarqueV(dataToEdit))
  };


    return (
      <div className="p-16 sm:p-24 max-w-2xl">
        <div className="min-h-72 h-72 sm:h-136 sm:min-h-136">
          { 
                marqueId === 'add' ? (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={submitHandler}>
                    <span className="hidden sm:flex">Ajouter une marque</span>
                  </Button>
                ) : (
                  <Button className="whitespace-no-wrap" variant="contained" onClick={editHandler}>
                    <span className="hidden sm:flex">Modifier une marque</span>
                  </Button>
                )
            }
        </div>
        <form>
          <TextField  
            id="nomMarque"
            value={nomMarque}
            label="Nom"        
            variant="outlined"
            margin="normal" 
            type="text"
            className="mt-8 mb-16"   
            fullWidth
            onChange={(e) => setNomMarque(e.target.value)}
          />
        </form>
      </div>
    )
}
