/* eslint-disable no-shadow */
/* eslint-disable no-redeclare */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useEffect, useState } from 'react';
import {Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { withStyles, makeStyles } from '@material-ui/core/styles';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Fab } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { getAllDepartements, selectDepartement, removeDepartement } from '../../store/epics/departementsActions'

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
  fabdelete: {
    backgroundColor: 'red'
  },
});

export default function TableDepartments() {
    const classes = useStyles();

    const departementV = useSelector((state) => state.departementV);
    const { allDepartements } = departementV;

    const dispatch = useDispatch();

    useEffect(() => {
      dispatch(getAllDepartements())
    }, [dispatch])

    const deleteHandler = (departement) => {
      if (window.confirm('Are you sure to delete?')) {
        dispatch(removeDepartement(departement.id));
      }
    };
    return (
      <div>
        <TableContainer component={Paper}>
          <Table className={classes.table} aria-label="customized table">
            <TableHead>
              <TableRow>
                <StyledTableCell>Nom</StyledTableCell>
                <StyledTableCell align="right">        
                  <Button component={Link} to="/form-department/add" variant="contained" size="small">
                    Ajouter Department
                  </Button>
                </StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                allDepartements.map((dep) => (
                  <StyledTableRow key={dep.id}>
                    <StyledTableCell component="th" scope="row">
                      {dep.nomDepartement}
                    </StyledTableCell>
                    <StyledTableCell align="right">
                      <Fab 
                        size="small" 
                        color="primary" 
                        aria-label="edit" 
                        className={classes.fabedit} 
                        onClick={() => selectDepartement(dep)}
                        component={Link} 
                        to={'/form-department/' + dep.id}
                      >
                        <EditIcon />
                      </Fab>
                      <Fab 
                        size="small" 
                        aria-label="delete" 
                        color="primary" 
                        className={classes.fabdelete}
                        onClick={() => deleteHandler(dep)}
                      >
                        <DeleteIcon />
                      </Fab>
                    </StyledTableCell>
                  </StyledTableRow>
                ))
            }
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    )
}
